<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-基本使用1">基本使用</a></li><li><a href="#vue-doc-每页数量1">每页数量</a></li><li><a href="#vue-doc-电梯1">电梯</a></li><li><a href="#vue-doc-总数1">总数</a></li><li><a href="#vue-doc-迷你型1">迷你型</a></li><li><a href="#vue-doc-简洁1">简洁</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-page分页1" class="vue-doc-element">Page 分页</h1>
<p class="vue-doc-element">当数据量较多时，使用分页可以快速进行数据切换。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-button1" class="vue-doc-element">Button</h1>
<p class="vue-doc-element">Basic component that is used to trigger bussiness logic.</p>

        </div>

        <div class="vue-doc-components">
          <demobasic></demobasic>
<demosize></demosize>
<demoelevator></demoelevator>
<demototal></demototal>
<demosmall></demosmall>
<demosimple></demosimple>
        </div>


        <div class="vue-doc-content">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<h3 id="vue-doc-pageprops1_1" class="vue-doc-element">Page props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>current</td>
<td>当前页码，支持 .sync 修饰符</td>
<td>Number</td>
<td>1</td>
</tr>
<tr>
<td>total</td>
<td>数据总数</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>page-size</td>
<td>每页条数</td>
<td>Number</td>
<td>10</td>
</tr>
<tr>
<td>page-size-opts</td>
<td>每页条数切换的配置</td>
<td>Array</td>
<td>[10, 20, 30, 40]</td>
</tr>
<tr>
<td>placement</td>
<td>条数切换弹窗的展开方向，可选值为 bottom 和 top</td>
<td>String</td>
<td>bottom</td>
</tr>
<tr>
<td>size</td>
<td>可选值为small（迷你版）或不填（默认）</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>simple</td>
<td>简洁版</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>show-total</td>
<td>显示总数</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>show-elevator</td>
<td>显示电梯，可以快速切换到某一页</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>show-sizer</td>
<td>显示分页，用来改变page-size</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>class-name</td>
<td>自定义 class 名称</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>styles</td>
<td>自定义 style 样式</td>
<td>Object</td>
<td>-</td>
</tr>
<tr>
<td>transfer</td>
<td>是否将弹层放置于 body 内，在 Tabs、带有 fixed 的 Table 列内使用时，建议添加此属性，它将不受父级样式影响，从而达到更好的效果</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-pageevents1_2" class="vue-doc-element">Page events</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>页码改变的回调，返回改变后的页码</td>
<td>页码</td>
</tr>
<tr>
<td>on-page-size-change</td>
<td>切换每页条数时的回调，返回切换后的每页条数</td>
<td>page-size</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-pageslot1_3" class="vue-doc-element">Page slot</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>无</td>
<td>自定义显示总数的内容</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th>Attribute</th>
<th style="text-align:right">Explanation</th>
<th>Type</th>
<th>Default</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td style="text-align:right">Button type, options include primary、ghost、dashed、text、info、success、warning、error, optional</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
import demosize from './demo/size.md';
import demoelevator from './demo/elevator.md';
import demototal from './demo/total.md';
import demosmall from './demo/small.md';
import demosimple from './demo/simple.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,demosize,demoelevator,demototal,demosmall,demosimple
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


